import React, { PureComponent } from "react";
import HotSpot from "../../Elements/HotSpot";
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IS_SCREEN_SHOT, marginTop_mainDiv } from '../../../../utils'

export default class Image extends PureComponent {

  componentDidMount(){
    marginTop_mainDiv(94)
  }

  render() {
    const { ImageClass, ImageUrl, height,width , item, ugc_products, wall} = this.props

 
    const styleImg = { height: item.hotspot ? `100%` : `auto`, width: item.hotspot ? `100%` : `100%`, margin: `0px auto` }
    const imageStyle = {
      paddingBottom: height == "" ? height+'%' : height  + '%', width: '100%'
    };
    return (
      <div className="tb_cp_image" style={{imageStyle}}  >
        <img loading="lazy" data-src={ImageUrl}  src={ImageUrl}  data-is-optmized="0" className={`imgID${item.id}`} style={styleImg} data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} onLoad={this.onLoadImage} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId}  height={height} width={width} onError={(e) => { if(item.networkId !== `28`) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e) }} alt="image"/>
        {item.hotspot ?<HotSpot ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} item={item} /> :null }
      </div>
    );
  }
}