import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import SocialCardMedia from "./Media";
import SocialShare from "../../Elements/SocialShare";
import CardIcons from "../../Elements/CardIcons";
import { connect } from 'react-redux';
import SocialActions from '../../Elements/SocialAction'
import { findNetworkDataFromWebFilters, findFromArray , removeTransFromCss, isPostAvailable} from '../../../../utils'
import { themePostTracking, showPopUP } from '../../../../../../../../../../actions/themeActions/themeActions'
import { RATINGS_PATH } from '../../../../constants' 


class Card extends PureComponent {
  constructor(props) {
    super(props);
    this.myRef_gallery = React.createRef();
    this.state = {cardWidth : null}
  }
  componentDidMount(){ 
    if(isPostAvailable(this.props.itemData.id)){
      this.onLoadPopup();
      setTimeout(()=>{
        const element = document.querySelector(`.ts_p_${this.props.itemData.id}`);
        if(element){element.scrollIntoView(true);}
      if(this.myRef_gallery && this.myRef_gallery.current) this.setState({cardWidth: this.myRef_gallery.current.clientWidth})
    },500)
    }
  }
  componentDidUpdate(){
    setTimeout(() => {
      // console.log('width', this.myRef_gallery && this.myRef_gallery.current ? this.myRef_gallery.current.clientWidth : null)
      if(this.myRef_gallery && this.myRef_gallery.current) this.setState({cardWidth: this.myRef_gallery.current.clientWidth})
    }, 1000);
  }
  onLoadPopup = event => {
    const { itemData, wall, appendData, postData, itemIndex, languageSetting, completeDataObject, wallId, webFilters } = this.props;
    let updatePostData = postData.map(itemData => completeDataObject[itemData])
    let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
    let currentIndex = findFromArray(filteredPostData, itemData.id, itemIndex)
    if (wall.Personalization.postFeatured === 1) {
      removeTransFromCss(`add`);
      this.props.showPopUP({ type: 'post', card: itemData, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, ThemeRule: wall.ThemeRule, webFilters: webFilters, UserRule: wall.UserRule, wall: wall })
    } themePostTracking({ action: 2, wall: wallId, feed: itemData.feedId, post: itemData.referenceId })
  }
  render() {    
    const { itemData, itemIndex, wall, webFilters, adjustWidth } = this.props;
    const highlight = itemData.highlight; const shareOption = wall.ThemeRule.shareOption; const hideContent = wall.ThemeRule.hideContent;
    const networkData =   findNetworkDataFromWebFilters(itemData.network, webFilters)
    const ts_cp_post_in = { backgroundColor: wall.ThemeRule.cardColor };
    const cardWidth = { width: highlight == 1 ? (adjustWidth * 2) : adjustWidth, padding: wall.Personalization.padding / 2 }
    const socialAction = wall.ThemeRule.socialAction;
    const tb_post_rating ={backgroundImage: `url(${RATINGS_PATH}${itemData.networkId}/${itemData.rating}.png)`}
    
    return (
      <div className={`ts_cp_post_wrapper ${itemData && itemData.id ? "ts_p_"+itemData.id:"" }`} ref={this.myRef_gallery} style={cardWidth}>
          <div className="ts_cp_post_in tb__post_in" style={ts_cp_post_in}>
          <div onClick={itemData.isPost ? null : this.onLoadPopup}> 
              <SocialCardMedia cardWidth={this.state.cardWidth} item={itemData} wall={wall}/>
              <AuthorDetails wall={wall} item={itemData} network={networkData} />
              <div className="ts_cp_postContentCard_ratting">
              {itemData.rating ? <div className="tb_post_rating" style={tb_post_rating} ><div></div></div>:''}
              </div>
          </div>
          {!itemData.isPost && shareOption ? <SocialShare item={itemData}  wall={wall} network={networkData} /> : null}
          {networkData.id !== '29' && socialAction ? <SocialActions item={itemData} wall={wall} network={networkData} /> : null}
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => { return { showPopUP: (data) => dispatch(showPopUP(data)), } };
const mapStateToProps = state => { return { wallId: state.appData.wallID, } }
export default connect(mapStateToProps, mapDispatchToProps)(Card);