import React, { PureComponent } from "react";
import { isValidAuthorImage, TWITTER_LOGO_FN } from '../../../../utils';
import {convertHtmlStringToRender} from '../../../Components/customFunction'
import { themePostTracking } from '../../../../../../../../../../actions/themeActions/themeActions';
import moment from 'moment';
import AuthorAvatar from '../../Elements/AuthorAvatar'
import Time from "../../Elements/Time";
import { TWITTER_LOGO } from "../../../../../../../../../../actions/api";
const checkStringInNotNull = (string) => string != null && string !== "" && string !== 0 ? true : false;
export default class AuthorDetails extends PureComponent {
  state = { isAuthorImageValid: true }
  componentDidMount() {
    const { item } = this.props;
    const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
    this.setState({ isAuthorImagevalid: isAuthorImagevalid });
  }
  onCtaClick = event => {
    const { wall, item } = this.props;
    themePostTracking({
      type: 1,
      action: 2,
      wall: wall.Wall.id,
      feed: item.feedId,
      post: item.referenceId,
    })
  }
  render() {
    const { wall, item, network } = this.props;
    const { isAuthorImagevalid } = this.state; const postTime = wall.Personalization.postTime;
    const isCta = checkStringInNotNull(item.cta); 
    const isInstaUser = item.author.isInstaUser; 
    const postAuthor = wall.Personalization.postAuthor;
    const networkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor;
    const BGImage = { backgroundImage: `url(${item.author.picture})` };
    const tb_authorBgColor = { backgroundColor: wall.ThemeRule.authorColor };
    const tb_authorColor = { color: wall.ThemeRule.authorColor };
    const networkIcon = network.icon.replace('fa-', '');
    
    return (<>
      {isCta ? <span onClick={this.onCtaClick}> {convertHtmlStringToRender(item.cta)}</span> : null}
      <div className="ts_cp_author_wrapper">
        {!isInstaUser ?
          <div className="ts_cp_author">
             <div className="ts_cp_author_profile"><img loading="lazy"  className="ts_cp_author_profile_img" src={item.author.picture} onError={(e)=>{e.target.onerror = null;  e.target.src=item.author.errorPic}} alt="image" style={{margin:0}}/></div> 
            <div className="ts_cp_author_info">{postAuthor ? <div className="ts_cp_authorname" style={tb_authorColor}>{item.author.name}</div> : ''}
              <div className="ts_cp_post_info">
                {postAuthor ? <div className="ts_cp_username" style={tb_authorColor}>@{item.author.username}</div> : ''}
                {postTime ? <>
                  {postAuthor ? <div className="ts_cp_seprator" style={tb_authorBgColor}><div></div></div> : ''}
                  {postTime ? <Time postTime={item.createdAt} timeClass={'tb_sc_time'} timeStyle={tb_authorColor}/>:'' }
                </>
                  : ''}
              </div>
            </div>
          </div>
          : null}
        <div className="ts_cp_social_">
        <div className={`tb__icon tb-${networkIcon}`} style={{ color: networkColor }}><div>{networkIcon =='twitter' && <TWITTER_LOGO_FN color={networkColor}/> }</div></div>
        </div>
      </div>
    </>
    );
  }
}
